.container {
  text-align: center;
  max-width: 50%;
  background-color: #ddd;
  font-family: 'Inconsolata', monospace;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  border: 1px solid black;
  text-align: center;
  margin-top: 50px;
  margin-bottom: 100px;
  padding: 20px;
  padding-bottom: 40px;
  font-size: 1.2em;
  display: block;
  justify-content: center;
}

.container p {
  margin-top: 20px;
  margin-bottom: 20px;
}


.apply a {
  display: block;
  padding: 15px;
  text-decoration: none;
  color: black;
  font-weight: 800;
  text-transform: uppercase;
  margin: 0 10px;
  position: relative;
  color: white;
}

